"use client";

import { useEffect, useState, useRef } from "react";
import Input from "@/components/form/input/InputField";
import { SalesService } from "@/services/sales.service";
import toast from "react-hot-toast";
import { useRouter } from "next/navigation";


/* =========================
   Types
========================= */
interface SaleDto {
  saleId: string;
  invoiceNumber: string;
  customerName: string;
  branchName: string;
  subTotal: number;
  discount: number;
  totalAmount: number;
  paidAmount: number;
  balance: number;
  paymentStatus: "OPEN" | "PARTIAL" | "CLOSED" | "CANCELLED";
  saleDate: string;
}

/* =========================
   Status Badge
========================= */
function StatusBadge({ status }: { status: SaleDto["paymentStatus"] }) {
  const map: Record<SaleDto["paymentStatus"], string> = {
    OPEN: "bg-blue-100 text-blue-700",
    PARTIAL: "bg-yellow-100 text-yellow-700",
    CLOSED: "bg-green-100 text-green-700",
    CANCELLED: "bg-red-100 text-red-700",
  };

  return (
    <span className={`px-2 py-1 rounded text-xs font-medium ${map[status]}`}>
      {status}
    </span>
  );
}

/* =========================
   Action Menu
========================= */
function ActionMenu({ onView }: { onView: () => void }) {
  const [open, setOpen] = useState(false);
  const ref = useRef<HTMLDivElement>(null);

  useEffect(() => {
    const close = (e: MouseEvent) => {
      if (ref.current && !ref.current.contains(e.target as Node)) {
        setOpen(false);
      }
    };
    document.addEventListener("mousedown", close);
    return () => document.removeEventListener("mousedown", close);
  }, []);

  return (
    <div ref={ref} className="relative">
      <button
        onClick={() => setOpen((p) => !p)}
        className="rounded-full px-2 hover:bg-gray-100"
      >
        ‚ãÆ
      </button>

      {open && (
        <div className="absolute right-0 z-50 w-32 rounded border bg-white shadow">
          <button
            onClick={() => {
              onView();
              setOpen(false);
            }}
            className="w-full px-4 py-2 text-left hover:bg-gray-100"
          >
            üëÅ View
          </button>
        </div>
      )}
    </div>
  );
}

/* =========================
   Sales Table (DEFAULT EXPORT)
========================= */
export default function SalesTable() {
  const [sales, setSales] = useState<SaleDto[]>([]);
  const [search, setSearch] = useState("");
  const [loading, setLoading] = useState(false);

  const router = useRouter();


  /* =========================
     LOAD SALES
  ========================= */
  async function loadSales() {
    try {
      setLoading(true);

      const res = await SalesService.getAllSales();

      // ‚úÖ API RESPONSE: { data: SaleDto[] }
      const salesList = res?.data?.data;

      setSales(Array.isArray(salesList) ? salesList : []);
    } catch (error) {
      console.error("Sales load error:", error);
      toast.error("Failed to load sales");
      setSales([]); // üîí never allow undefined
    } finally {
      setLoading(false);
    }
  }

  /* =========================
     CALL API ON MOUNT
  ========================= */
  useEffect(() => {
    loadSales();
  }, []);

  /* =========================
     FILTER
  ========================= */
  const filteredSales = sales.filter(
    (s) =>
      s.invoiceNumber.toLowerCase().includes(search.toLowerCase()) ||
      s.customerName.toLowerCase().includes(search.toLowerCase()) ||
      s.branchName.toLowerCase().includes(search.toLowerCase())
  );

  return (
    <div className="bg-white p-6 rounded-xl">
      <h2 className="mb-4 text-xl font-semibold">Sales</h2>

      {/* Search */}
      <Input
        placeholder="Search invoice, customer, branch..."
        value={search}
        onChange={(e) => setSearch(e.target.value)}
        className="w-72 mb-4"
      />

      {/* Table */}
      <div className="overflow-x-auto">
        <table className="w-full border-collapse">
          <thead className="bg-gray-50">
            <tr>
              <th className="px-3 py-2 text-left">Invoice</th>
              <th className="px-3 py-2 text-left">Customer</th>
              <th className="px-3 py-2 text-left">Branch</th>
              <th className="px-3 py-2 text-right">Total</th>
              <th className="px-3 py-2 text-right">Paid</th>
              <th className="px-3 py-2 text-right">Balance</th>
              <th className="px-3 py-2 text-left">Status</th>
              <th className="px-3 py-2 text-left">Date</th>
              <th className="px-3 py-2 text-right">Action</th>
            </tr>
          </thead>

          <tbody>
            {loading ? (
              <tr>
                <td colSpan={9} className="py-6 text-center">
                  Loading sales...
                </td>
              </tr>
            ) : filteredSales.length === 0 ? (
              <tr>
                <td colSpan={9} className="py-6 text-center text-gray-500">
                  No sales found
                </td>
              </tr>
            ) : (
              filteredSales.map((s) => (
                <tr key={s.saleId} className="border-t">
                  <td className="px-3 py-2 text-sm">{s.invoiceNumber}</td>
                  <td className="px-3 py-2 text-sm">{s.customerName}</td>
                  <td className="px-3 py-2 text-sm">{s.branchName}</td>
                  <td className="px-3 py-2 text-sm">{s.totalAmount}</td>
                  <td className="px-3 py-2 text-sm">{s.paidAmount}</td>
                  <td className="px-3 py-2 text-sm">{s.balance}</td>
                  <td className="px-3 py-2 text-sm">
                    <StatusBadge status={s.paymentStatus} />
                  </td>
                  <td className="px-3 py-2 text-sm">
                    {new Date(s.saleDate).toLocaleDateString()}
                  </td>
                  <td className="px-3 py-2 text-sm">
                 <ActionMenu
  onView={() => router.push(`/Sales/Detail/${s.saleId}`)}
/>

                  </td>
                </tr>
              ))
            )}
          </tbody>
        </table>
      </div>
    </div>
  );
}
